import React from 'react'

export const ViewMoreLess = (props: any) => {
  const flip = { transform: 'rotate(180deg)' };

  return (
    <div className='flex flex-col w-full h-full justify-center items-center'>
      <input className={`view-more-less-${props.index} hidden`} type='checkbox' id={`view-more-less-${props.index}`} />
      <label className={`view-more-${props.index} w-full border-white/20 border-b whitespace-nowrap`} htmlFor={`view-more-less-${props.index}`} aria-label='toggle view more'>
        <div className='inline-block mr-4' style={flip}>^</div>view-more<div className='inline-block ml-4' style={flip}>^</div>
      </label>
      <div className='view-content flex flex-col justify-center items-start text-start'>
        {props.children}
      </div>
      <label className={`view-less-${props.index} hidden w-full border-white/20 border-t whitespace-nowrap`} htmlFor={`view-more-less-${props.index}`} aria-label='toggle view less'>
        <div className='mr-4'>^</div>view-less<div className='ml-4'>^</div>
      </label>
    </div>
  )
};

export default ViewMoreLess;